"use client"
import React, { useContext, useState, useEffect } from "react";
import { CartContext, useAppContext } from "#/widgets/context";
import { Item } from "#/types";
import Link from "next/link";
import Cookies from 'js-cookie';

interface GuitarCardProps extends Item {}

export function GuitarCard({
  id,
  imageUrl,
  category,
  title,
  price,
}: GuitarCardProps) {
  const { onAddToCart, isItemInCart } = useAppContext();
  const [isFavorite, setIsFavorite] = useState(false);

  const isAdded = isItemInCart(title);

  // Проверяем при загрузке, есть ли товар в избранном
  useEffect(() => {
    const favorites = getFavorites();
    setIsFavorite(favorites.includes(id));
  }, [id]);

  // Функции для работы с избранным
  const getFavorites = (): string[] => {
    const favoritesCookie = Cookies.get('favorites');
    return favoritesCookie ? JSON.parse(favoritesCookie) : [];
  };

  const toggleFavorite = () => {
    let favorites = getFavorites();
    
    if (isFavorite) {
      favorites = favorites.filter(favId => favId !== id);
    } else {
      favorites = [...favorites, id];
    }
    
    Cookies.set('favorites', JSON.stringify(favorites), { expires: 365 });
    setIsFavorite(!isFavorite);
  };

  const onAdd = () => {
    onAddToCart({ id, imageUrl, category, title, price });
  };

  const formattedPrice = price.toLocaleString("ru-RU");

  return (
    <div className="card" key={id}>
      <Link href={`/tovar/${encodeURIComponent(title)}`}>
        <button className="img">
          <img src={imageUrl} alt="products" />
        </button>
      </Link>
      
      <button onClick={toggleFavorite} className="favorite-button">
        {isFavorite ? (
          <img className="img5" src="/icons/onFavourite.svg" alt="Remove from Favorites" />
        ) : (
          <img className="img5" src="/icons/favourite.svg" alt="Add to Favorites" />
        )}
      </button>
      
      <p>{category}</p>
      <a>{title}</a>
      <b>{formattedPrice} ₽.</b>
      <button className="add">
        {!isAdded ? (
          <button onClick={() => onAdd()}>
            <img className="img2" src="/pictures/toCart.svg" alt="Add to Cart" />
          </button>
        ) : (
          <Link href="/cart">
            <img className="img2" src="/pictures/inCart.svg" alt="In Cart" />
          </Link>
        )}
      </button>
    </div>
  );
}